.App {
  background: #343a40;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  overflow-x: hidden;
}

@media (max-width: 1000px) {
  html {
    font-size: 80%;
  }
}

button {
  font-weight: bold;
  font-size: 1.1rem;
  cursor: pointer;
  padding: 1rem 2rem;
  border: 3px solid #23d997;
  background: transparent;
  color: white;
  transition: all 0.5s ease;
}
button:hover {
  background-color: #23d997;
  color: black;
}

h2 {
  font-weight: lighter;
  color: white;
}

h3 {
  color: white;
}

h4 {
  font-weight: bold;
  font-size: 2rem;
}

span {
  color: #23d997;
  font-size: 2rem;
}

p {
  padding: 3rem 0rem;
  color: #ccc;
  font-size: 1.4rem;
  line-height: 150%;
}
.movie-details {
  color: white;
  width: 100%;
  flex-wrap: wrap;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  overflow: hidden;
}
.movie-details h3 {
  text-align: center;
}

.headline {
  min-height: 90vh;
  width: 90%;
  margin-left: 5%;
  padding-top: 20vh;
  position: relative;
}
.headline h2 {
  position: absolute;
  top: 10%;
  left: 50%;
  -webkit-transform: translate(-50%, -10%);
          transform: translate(-50%, -10%);
}
.headline img {
  width: 100%;
  height: 70vh;
  object-fit: cover;
}

.awards {
  min-height: 80vh;
  display: block;
  margin: 2rem 2rem;
  align-items: center;
  justify-content: space-between;
}
@media (max-width: 1000px) {
  .awards {
    display: block;
    margin: 2rem 2rem;
  }
}

.award {
  padding: 2rem;
}
.award h3 {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  font-size: 2rem;
  text-align: left;
  border-bottom: 5px solid #23d997;
}
.award .line {
  width: 30%;
  background: #23d997;
  height: 0.5rem;
  margin: 1rem 0rem;
}
.award p {
  padding: 2rem 0rem 2rem 0rem;
}

.picture {
  min-height: 50vh;
}
.picture img {
  width: 100%;
  height: 100vh;
  object-fit: cover;
}
.about {
  min-height: 90vh;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 5rem 4rem;
  color: white;
  background-color: #343a40;
}
@media (max-width: 1000px) {
  .about {
    display: block;
    padding: 2rem 2rem;
    align-items: center;
    text-align: center;
  }
}
.about img {
  z-index: 2;
  overflow: hidden;
  width: 50%;
  height: 100%;
  box-shadow: 5px 5px 15px 7px #FFFFFF;
  object-fit: cover;
}
@media (max-width: 1000px) {
  .about img {
    margin-top: 2em;
    z-index: 10;
    width: 100%;
    margin: 2rem 0rem 3rem 0rem;
  }
}
.about .hide {
  overflow: hidden;
}

.description {
  z-index: 2;
  flex: 1 1;
  padding-right: 2rem;
}
.description h2 {
  font-weight: lighter;
}
.wave {
  position: absolute;
  left: 0;
  z-index: 1;
}
.services {
  min-height: 90vh;
  display: flex;
  justify-content: space-between;
  padding: 5rem 2rem;
  color: white;
  background-color: #343a40;
}
.services h2 {
  padding-bottom: 5rem;
}
.services p {
  width: 70%;
  padding: 2rem 0rem 4rem 0rem;
}
.services .hide {
  overflow: hidden;
}

.description {
  float: right;
  flex: 1 1;
  padding-right: 2rem;
}
.description h2 {
  font-weight: lighter;
}

.cards {
  margin-top: 140px;
  display: flex;
  flex-wrap: wrap;
  grid-gap: 1em;
  gap: 1em;
}
@media (max-width: 1500px) {
  .cards {
    justify-content: center;
  }
}
.cards .card {
  border: 2px;
  border: 3px solid #23d997;
  background-color: #1b1b1b;
  flex-basis: 15rem;
  width: 100%;
  height: 10em;
  object-fit: cover;
  overflow: hidden;
}
.cards .card h3 {
  text-align: center;
}
.cards .icon {
  display: flex;
  justify-content: center;
}
.cards img {
  margin-top: 1px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: 40px;
  object-fit: cover;
}

.image {
  float: right;
  width: 40em;
  padding-top: 10rem;
  height: 100vh;
}
.image img {
  width: 100%;
  height: 100vh;
  overflow: hidden;
}
.faq {
  margin-top: 10em;
  color: white;
  background-color: #343a40;
  padding: 1rem 3rem;
}

span {
  display: block;
  font-weight: bold;
}

h2 {
  padding-bottom: 2rem;
  font-weight: bold;
}

.faq-line {
  background: #cccccc;
  height: 0.2rem;
  margin: 2rem 0rem;
  width: 100%;
}

.question {
  padding: 3rem 0rem;
  cursor: pointer;
}

.answer {
  padding: 2rem 0rem;
}
.answer p {
  padding: 1rem 0rem;
}
.nav-bar {
  min-height: 10vh;
  width: 100%;
  display: flex;
  margin: auto;
  background-color: #495057;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 10rem;
  position: fixed;
  top: 0;
  z-index: 10;
}
.nav-bar a {
  color: white;
  text-decoration: none;
}
.nav-bar ul {
  display: flex;
  list-style: none;
  justify-content: space-between;
}
.nav-bar #logo {
  font-size: 1.8rem;
  font-family: "Lobster", cursive;
  font-weight: lighter;
}
.nav-bar li {
  padding-left: 10rem;
  position: relative;
}
@media (max-width: 1000px) {
  .nav-bar {
    flex-direction: column;
    padding: 0.2rem;
  }
  .nav-bar #logo {
    display: inline-block;
    margin: 2rem;
  }
  .nav-bar ul {
    padding: 2rem;
    justify-content: space-around;
    width: 100%;
  }
  .nav-bar li {
    padding: 0;
  }
}

.line-active {
  height: 0.3rem;
  background: #23d997;
  width: 5%;
  position: absolute;
  bottom: -80%;
  left: 60%;
}
@media (max-width: 1000px) {
  .line-active {
    left: 0%;
  }
}
.my_work {
  min-height: 100vh;
  background-color: #343a40;
  overflow: hidden;
  padding: 5rem 10rem;
}
.my_work h2 {
  padding: 1rem 0rem;
}
@media (max-width: 1000px) {
  .my_work {
    padding: 2rem 2rem;
  }
}

.movie {
  padding-bottom: 10rem;
  background-color: #343a40;
}
.movie .line {
  height: 0.5rem;
  background: #23d997;
  margin-bottom: 3rem;
}
.movie img {
  width: 100%;
  height: 70vh;
  object-fit: cover;
}

img {
  height: 20em;
  width: 70%;
}

.hidden {
  overflow: hidden;
}

.frame1 {
  position: fixed;
  left: 0;
  top: 10%;
  width: 100%;
  height: 100vh;
  background: #fffebf;
  z-index: 2;
}

.frame2 {
  position: fixed;
  left: 0;
  top: 10%;
  width: 100%;
  height: 100vh;
  background: #84a98c;
  z-index: 2;
}

.frame3 {
  position: fixed;
  left: 0;
  top: 10%;
  width: 100%;
  height: 100vh;
  background: #f5cac3;
  z-index: 2;
}

.frame4 {
  position: fixed;
  left: 0;
  top: 10%;
  width: 100%;
  height: 100vh;
  background: #669bbc;
  z-index: 2;
}
.contact-style {
  padding: 2rem 2rem;
  color: white;
  min-height: 100vh;
  padding: 2rem;
  font-size: 1rem;
}
@media (max-width: 1000px) {
  .contact-style {
    margin-top: 5rem;
    font-size: 1rem;
    padding: 2rem;
  }
}
.contact-style .title {
  margin-bottom: 4rem;
  color: white;
}
@media (max-width: 1000px) {
  .contact-style .title {
    margin-top: 5rem;
  }
}

.hide {
  overflow: hidden;
}

.social {
  word-wrap: break-word;
  display: flex;
  flex-direction: row;
  padding-top: 3em;
}
.social .circle {
  width: 3rem;
  height: 3rem;
  background-color: white;
}
.social h2 {
  margin-left: 3em;
  display: inline-block;
  white-space: nowrap;
}
